@import "../../../Sass/variables";

.card {
  border: 1px solid $border-color-grey;
  background-color: $background-white;
  margin-bottom: 3.5em;
  border-radius: 3px;

  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.75em;

    .profile {
      margin-bottom: 0;
    }

    .cardButton {
      height: 30px;

      &:hover {
        cursor: pointer;
      }
    }
  }

  .cardImage {
    width: 100%;
    display: block;
  }

  .likedBy {
    display: flex;
    padding-left: 1em;

    span {
      font-size: 0.75em;
      margin-left: 0.5em;
      padding-top: 0.5em;
    }
  }

  .comments {
    margin-bottom: 0.75em;
    max-height: 300px;
    overflow-y: auto;
  }

  .timePosted {
    font-size: 0.6em;
    padding-left: 2em;
    padding-bottom: 1em;
    color: $font-color-secondary;
    border-bottom: 1px solid $border-color-grey-secondary;
  }

  .addComment {
    display: flex;
    justify-content: space-between;
    font-size: 0.85em;
    padding: 1.5em 1.25em;
    position: relative;

    .commentText {
      color: $font-color-primary;
    }

    .postText-btn {
      color: $link-color;
      font-weight: 400;
      cursor: pointer;
      border: 0;
      background: transparent;
      &:focus{
          outline: 0;
      }
    }
  }
}

@media only screen and (max-width: 640px) {
  .card {
    border: none;
    background-color: $background-grey;
    // margin-bottom: 0.75em;
     margin-top: 15px;

     .cardImage {
      width: 100%;
      height: 400px;
      display: block;
      object-fit: cover;
      object-position: contain;
    }
     
     

    // .addComment {
    //   display: none;
    // }

    .timePosted {
      border: none;
    }
  }
}
