@import '../../Sass/variables';

.homeScreen{
  width: 100%;
  height: 100%;
  position: relative;
  padding-bottom: 2.5rem;
}

main {
  background-color: $background-grey;
  margin-top: 60px;
  height: 100vh;
 

  .container {
    display: flex;
    justify-content: left;
    margin: 0 auto;
    max-width: 1000px;
  }
}

a {
  text-decoration: none;
  color: $link-color;
  font-size: 0.9em;
  font-weight: 500;
}

@media only screen and (max-width: 1024px) {
  .homeScreen{
    main .container {
      justify-content: center !important;
    }

  }
}